import React from "react";
import Card from "../Card/Card";
import importAllImagesText from '../Helpers/ImportAllImagesText';

const localsImages = await importAllImagesText(require.context('../PartnersLogos/Locals', false, /\.(webp)$/));

const Locals = () => {
    return (
        <>
            <h2>Locals</h2>
            {!localsImages.length && <h4 style={{textAlign:'center'}}>No Partners Yet!</h4>}
            <div className="section-container">
                {localsImages.length > 0 &&  localsImages.map((localImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index}
                        image={localImage.image}
                        partner_name={localImage.name}
                    />
                ))}
            </div>
        </>
    );
}

export default Locals;